.footer {
  background: var(--primary-color);
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.topFooter {
  padding: 50px;
  display: flex;
  align-items: stretch;
}

.footerContactItem {
  flex: 1;
  padding: 7px;
  color: #f5f5f5;
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
  margin: 10px;
}

.contactItemDivider {
  background: #a1a1a1;
  height: 2px;
  width: 100%;
  display: block;
  margin-top: 10px;
  margin-bottom: 20px;
}

.footerLink {
  display: block;
  margin: 7px 0;
  color: #f3f3f3;
  text-decoration: none;
}

.socialIconsContainer {
  display: flex;
  align-items: center;
}
.socialIconsContainer a {
  text-decoration: none;
  margin-right: 7px;
}
.socialIcon {
  font-size: 30px;
}
.socialIcon:hover {
  opacity: 0.7;
}

.bottomFooter {
  text-align: center;
  text-decoration: none;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0px;
  background: #f5f5f5;
}

.bottomFooter a {
  color: black;
  margin-left: 3px;
}

@media (max-width: 850px) {
  .topFooter {
    padding: 25px;
    flex-direction: column;
  }
  .footerContactItem h5 {
    font-size: 22px;
  }
}
